@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
.rmdb-header {
	width: 100%;
	height: auto;
	background: #1C1C1C;
	padding: 0 20px;
	box-sizing: border-box;color:red;
}

.rmdb-header-content {
	max-width: 1280px;
	min-height: 120px;
	height: auto;
	padding: 20px 0px;
	margin: 0 auto;
	box-sizing: border-box;
	overflow: hidden;
}

.rmdb-logo {
	width: 300px;
	margin-top: 20px;
	float: left;
}

.rmdb-tmdb-logo {
	width: 122px;
	margin-top: 25px;
	float: right;
}

@media screen and (max-width: 500px) {
	.rmdb-header-content {
			max-width: 1280px;
			min-height: 0px;
	}

	.rmdb-tmdb-logo {
			display: inline-block;
			width:80px;
			margin-top: 0px;
	}

	.rmdb-logo {
			width: 150px;
			margin-top: 5px;
	}
}
.rmdb-grid h1 {
  font-family: "Abel", sans-serif;
  font-size: 42px;
  color: #a1a1a1;
}

.rmdb-grid-content {
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.rmdb-grid-element {
  margin: 0 40px 30px 0;
  background: #353535;
  max-height: 90%;
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s;
  overflow: hidden;
  border-radius: 10px;
}

.rmdb-grid-element:hover {
  box-shadow: 0 0 15px#1997c6;
  cursor: pointer;
  opacity: 0.8;
}

@-webkit-keyframes animateGrid {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateGrid {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* For a 4-column grid */
.rmdb-grid-element:nth-child(4n + 4) {
  margin-right: 0;
}

@media screen and (max-width: 768px) {
  .rmdb-grid-content {
    grid-template-columns: auto auto auto;
    grid-gap: 15px;
    gap: 15px;
    position: relative;
  }

  .rmdb-grid-element {
    max-height: 305px;
    -webkit-animation: animateGrid 0.5s;
            animation: animateGrid 0.5s;
    overflow: hidden;
    margin: 0;
    background: none !important;
    border-radius: 0;
  }
  .rmdb-loadmorebtn {
    width: 45% !important;
  }
  .rmdb-grid-element:nth-child(2n + 2) {
    margin-right: 0;
  }

  .rmdb-grid h1 {
    font-size: 22px;
  }
}

@media screen and (max-device-width: 480px) {
  .rmdb-grid-content {
    grid-template-columns: auto auto;
  }
  .rmdb-grid-element {
    max-height: 285px;
  }
  .rmdb-loadmorebtn {
    width: 65% !important;
  }
}

.rmdb-heroimage {
  background-size: 100%, cover !important;
  background-position: center, center !important;
  width: 100%;
  height: 600px;
  position: relative;
  -webkit-animation: animateHeroimage 1s;
          animation: animateHeroimage 1s;
}

@-webkit-keyframes animateHeroimage {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes animateHeroimage {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

.rmdb-heroimage-content {
  max-width: 1280px;
  padding: 20px;
  margin: 0 auto;
}

.rmdb-heroimage-text {
  z-index: 100;
  max-width: 700px;
  position: absolute;
  bottom: 40px;
  margin-right: 20px;
  min-height: 100px;
  background: rgba(0, 0, 0, 0.0);
  color: #fff;
}

.rmdb-heroimage-text h1 {
  font-family: 'Abel', sans-serif;
  font-size:48px;
  color: #fff;
}

.rmdb-heroimage-text p {
  font-family: 'Abel', sans-serif;
  font-size: 22px;
  line-height: 26px;
  color: #fff;
}

@media screen and (max-width: 720px) {
  .rmdb-heroimage-text {
    max-width: 100%;
  }

  .rmdb-heroimage-text h1 {
    font-size: 38px;
    color: #fff;
  }

  .rmdb-heroimage-text p {
    font-size: 16px;
    line-height: 20px;
    color: #fff;
  }
}
