  border-radius: 40px;
  position: relative;
  color: #fff;
}

.rmdb-fa-search {
  position: absolute;
  left: 20px;
  top: 12px;
  color: #fff;
}

.rmdb-searchbar-input {
  font-family: "Abel", sans-serif;
  font-size: 38px;
  position: absolute;
  left: 70px;
  top: 7px;
  border: 0;
  background: transparent;
  height: 40px;
  color: #fff;
}

.rmdb-searchbar-input:focus {
  outline: none;
}

@media only screen and (min-width: 360px) and (max-width: 767px) {
