  box-sizing: border-box;
}

.rmdb-actor-name {
  font-family: "Abel", sans-serif;
  font-size: 22px;
  color: skyblue;
  float: left;
  margin: 10px 10px;
  width: 50%;
  box-sizing: border-box;
}

